import { Link } from "gatsby"
import React from "react"
import Styles from "../styles/components/header.module.scss"
import classNames from "classnames"
import SlideMenu from "./slideMenu";

var contents = [
  {
    title: "りあん",
    link: "",
  },
  {
    title: "未来の保育士さんへ",
    link: "message",
  },
  {
    title: "スタッフインタビュー",
    link: "interview",
  },
  {
    title: "教室紹介",
    link: "classrooms",
  },
  {
    title: "見学",
    link: "visit"
  },
  {
    title: "制度",
    link: "welfare",
  },
  {
    title: "募集要項",
    link: "recruit",
  },
];

class Header extends React.Component {
  constructor(props) {
    super(props);

    this.url = this.props.url;
    console.log(this.url);

    this.contents = contents;
    this.setMenuClass();
  }

  isIn(word) {
    var splitedUrl = (typeof window !== 'undefined' && window.location.href).toString();
    if (splitedUrl.slice(-1) === '/') {
      splitedUrl = splitedUrl.slice(0, -1);
    }
    console.log(splitedUrl);
    return splitedUrl.match(word);
  }

  tailOf(number) {
    return this.splitedUrl[this.splitedUrl.length - number];
  }

  setMenuClass() {
    var rootLessContents = this.contents;
    // var findCurrent = false;
    rootLessContents.map((content) => {
      // const result = this.isIn(content.link);
      if (content.link === this.url) {
        content.classes = classNames(
          Styles.listItem,
          Styles.current
        );
        // findCurrent = true;
      } else {
        content.classes = classNames(
          Styles.listItem
        );
      }
    });
    // if (!findCurrent) {
    //   this.contents[0].classes = classNames(
    //     Styles.listItem,
    //     Styles.current
    //   );
    // } else {
    //   this.contents[0].classes = classNames(
    //     Styles.listItem
    //   );
    // }
  }

  hero() {
    return (
      <div className={Styles.hero}>
        <Link to={"/"} className={Styles.hero__title}>
          りあん RECRUIT
        </Link>
      </div>
    );
  }

  headerImage() {
    var headerIamge = (
      <div className={Styles.homeImage}>
        <div className={Styles.homeImage__message}>
          <strong>夢と誇りを持てる<br />社会のために</strong>
        </div>
      </div>
    );
    if (this.url === this.contents[1].link || this.url === this.contents[2].link || this.url === this.contents[3].link) {
      return (
        <div className={Styles.firstImage}></div>
      );
    } else if (this.url === this.contents[4].link || this.url === this.contents[5].link || this.url === this.contents[6].link) {
      return (
        <div className={Styles.secondImage}></div>
      );
    }
    return headerIamge;
  }

  headerMenu() {
    const listItems = this.contents.map((content) => 
      <div 
        key={content.link} 
        className={content.classes}>
        <Link to={content.link} className={Styles.link}>{content.title}</Link>
      </div>
    );
    return(
      <div className={Styles.headerMenu}>{listItems}</div>
    )
  }

  render() {
    this.result1 = (this.isIn(this.contents[1].link) || this.isIn(this.contents[2].link) || this.isIn(this.contents[3].link))? true : false;
    this.result2 = (this.isIn(this.contents[4].link) || this.isIn(this.contents[5].link) || this.isIn(this.contents[6].link))? true : false;
    console.log(this.result1);
    console.log(this.result2);
    return (
      <header>
        <SlideMenu />
        {this.hero()}
        {this.headerImage()}
        {this.headerMenu()}
      </header>
      
    );
  }
}

export default Header
